.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  height: 80px;
  background-image: linear-gradient(to top, #d2fffc, #00b4ff);
  margin-bottom: 10px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.search-form {
  width: 300px;
  align-items: center;
  justify-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.search-form__input {
  width: 100%;
  height: 30px;
  background-color: #fff;
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  outline: none;
  padding-left: 10px;
  padding-right: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.search-form__btn {
  width: 30px;
  height: 30px;
  background-color: rgba(0, 180, 255, .5);
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.gallery {
  max-width: 1200px;
}

@media screen and (min-width: 1200px) {
  .gallery {
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: -30px;
    margin-left: -30px;
    display: flex;
  }
}

.gallery__link {
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  display: flex;
}

@media screen and (max-width: 1199px) {
  .gallery__link:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery__link {
    margin-bottom: 30px;
    margin-left: 30px;
  }
}

.gallery__link:hover {
  cursor: zoom-in;
}

.gallery__thumb {
  overflow: hidden;
}

@media screen and (min-width: 1200px) {
  .gallery__thumb {
    height: 150px;
  }
}

.photo-card {
  max-width: 640px;
  object-fit: cover;
  flex-direction: column;
  justify-items: center;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

@media screen and (min-width: 1200px) {
  .photo-card {
    width: 270px;
  }
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery-info__box {
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.gallery-info__item {
  text-align: center;
  color: #000;
  font-size: 14px;
  font-weight: 500;
}

@media screen and (min-width: 1200px) {
  .gallery-info__item {
    font-size: 10px;
  }
}

.footer {
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.load-more {
  color: #000;
  width: 140px;
  height: 30px;
  background-color: rgba(0, 180, 255, .5);
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.load-more.is-hidden {
  display: none;
}

